<template>
  <div class="loading" v-if="loading">
      <h5>Loading.....</h5>
  </div>

  <div class="content">
       
      <div class="movie-card">
     <div class="col md-3">

     </div>

<div class="col-md-5">

  
</div>
    <div class="container">
      <img
          :src="info.data.movie.medium_cover_image"
