          <div class="title1">{{info.data.movie.title.slice(0,25)}} <span>{{info.data.movie.mpa_rating}}</span></div>

          <div class="title2">{{info.data.movie.title_long}}</div>

          <fieldset class="rating">
            <input type="radio" id="star5" name="rating" value="5" /><label
              class="full"
              for="star5"
              title="Awesome - 5 stars"
            ></label>
            <input
              type="radio"
              id="star4half"
              name="rating"
              value="4 and a half"
            /><label
              class="half"
              for="star4half"
              title="Pretty good - 4.5 stars"
            ></label>
            <input
              type="radio"
              id="star4"
              name="rating"
              value="4"
              checked
            /><label
              class="full"
              for="star4"
              title="Pretty good - 4 stars"
            ></label>
            <input
              type="radio"
              id="star3half"
              name="rating"
              value="3 and a half"
            /><label
              class="half"
              for="star3half"
              title="Meh - 3.5 stars"
            ></label>
            <input type="radio" id="star3" name="rating" value="3" /><label
              class="full"
              for="star3"
              title="Meh - 3 stars"
            ></label>
            <input
              type="radio"
              id="star2half"
              name="rating"
              value="2 and a half"
            /><label
              class="half"
              for="star2half"
              title="Kinda bad - 2.5 stars"
            ></label>
            <input type="radio" id="star2" name="rating" value="2" /><label
              class="full"
              for="star2"
              title="Kinda bad - 2 stars"
            ></label>
            <input
              type="radio"
              id="star1half"
              name="rating"
              value="1 and a half"
            /><label
              class="half"
              for="star1half"
              title="Meh - 1.5 stars"
            ></label>
            <input type="radio" id="star1" name="rating" value="1" /><label
              class="full"
              for="star1"
              title="Sucks big time - 1 star"
            ></label>
            <input
              type="radio"
              id="starhalf"
              name="rating"
              value="half"
            /><label
              class="half"
              for="starhalf"
              title="Sucks big time - 0.5 stars"
            ></label>
          </fieldset>

          <span class="likes">{{info.data.movie.like_count}} likes</span>
        </div>
        <!-- end details -->
      </div>
      <!-- end hero -->

      <div class="description">
        <div class="column1">
          <span class="tag">action</span>
          <span class="tag">fantasy</span>
          <span class="tag">adventure</span>
        </div>
        <!-- end column1 -->

        <div class="column2">
          <p>
              {{info.data.movie.description_intro.slice(0,200)}}
              
              
              <a href="#">read more</a>
          </p>

          <div class="avatars" style="display:inline-flex" v-for="item in info.data.movie.cast" :key="item.name">
            <a href="#"   :data-tooltip="item.name" data-placement="top"  >
              <img
                :src="item.url_small_image"
                :alt="item.name" width="50" height="50"
                style="over-flow:hidden;"
              />
            </a>

            
          </div>
          <!-- end avatars -->
        </div>
        <!-- end column2 -->
      </div>
      <!-- end description -->
    </div>
<div class="trailer" style="padding-top:55px">
  
      <iframe width="800" height="318" :src="'https://www.youtube.com/embed/'+info.data.movie.yt_trailer_code" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

  </div>    <!-- end container -->
  </div>
  <!-- end movie-card -->
  <h6 style="color:#42b983;font-family:monospace;font-weight:700;font-size:25px">Downloads</h6>
  <div class="downloads" v-for="item in info.data.movie.torrents" :key="item.name">
      <div class="part">
          <h6 style="color:#42b983;font-family:monospace;font-weight:700;font-size:25px">size : {{item.size}}</h6>
      <a class="btn btn-outline-success" :href="item.url">Download {{item.quality}}</a> 
      </div>
  </div>


  
 </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "Detail",
  // https://www.youtube.com/watch?v=XqffzRFzdco
  props: ["id"],
  data() {
    return {
      loading: true,
    
      info: "",
      related:"",
    };
  },
  components: {
   
  },
  methods: {
    //  moviedetail(e){
    //    alert(e);
    //  }
    //    ,
    
    
  },

  async created() {
    var url =
      "https://yts.mx/api/v2/movie_details.json?&with_images=true&with_cast=true&movie_id=" +this.id
     
    axios
      .get(url)
      .then(
        (response) => ((this.info = response.data), (this.loading = false))
      );

      var url2 = "https://yts.mx/api/v2/movie_suggestions.json?movie_id="+this.id
      axios.get(url2).then(
        (response)=>((this.related = response.data),(this.loading=false))
      )
  },


};
</script>



<style  scoped>
